import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom"
import './tailwind.css';
import Header from './components/Header'
import Footer from './components/Footer'
import Test from './components/Test'
import HelloWorld from './components/HelloWorld'

function App() {
  return (
    <div className="relative pb-10 min-h-screen">
    <Router>
      <Header />

      <div className="p-3">
      <Switch>
        <Route exact path="/">
          <HelloWorld name={"popo"} />
        </Route>
        <Route path="/2020/07/30">
          <Test title={"2020/07/30"} num={0} />
        </Route>
        <Route path="/2020/08/13">
          <Test title={"2020/08/13"} num={4} />
        </Route>
      </Switch>
      </div>

      <Footer />
    </Router>
    </div>
  );
}

export default App;
